@import "Variables";


.navTitle {
  background: transparent;
  position: fixed;
  top: 40px;
  left: 40px;
  z-index: 6;
  font-size: 20px;
  font-weight: 900;
  font-family: sans-serif;
  text-transform: uppercase;
  color: #ffffff;

  > span {
    text-transform: none;
    opacity: .5;
    font-weight: 300;
    font-size: 12px;
  }
}

input + label {
  position: fixed;
  top: 40px;
  right: 40px;
  height: 20px;
  width: 15px;
  z-index: 5;

  span {
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    margin-top: -1px;
    left: 0;
    display: block;
    background: #ffffff;
    transition: .5s;
  }

  span:first-child {
    top: 3px;
  }

  span:last-child {
    top: 16px;
  }
}

label:hover {
  cursor: pointer;
}

input:checked + label {
  span {
    opacity: 0;
    top: 50%;
  }

  span:first-child {
    opacity: 1;
    transform: rotate(405deg);
  }

  span:last-child {
    opacity: 1;
    transform: rotate(-405deg);
  }
}

input ~ .about-container {
  background: $primary_background;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 3;
  transition: .5s;
  transition-delay: .5s;
  overflow: hidden;

  > .about-1 {
    text-align: center;
    position: absolute;
    top: 35%;
    left: 20%;
    right: 20%;

    > p {
      opacity: 0;
      transition: .5s;
      transition-delay: 0s;

      > a {
        text-decoration: none;
        text-transform: uppercase;
        color: black;
        font-weight: 700;
        font-family: sans-serif;
        display: block;
        padding: 30px;
      }
    }
  }
}


input:checked ~ .about-container {
  height: 100%;
  transition-delay: 0s;
  background: #70798C;

  > .about-1 {
    > p {
      opacity: 1;
      transition-delay: .5s;
    }
  }
}

